import * as React from "react";
import moment from "moment";
import { Button, Input, message, Tooltip } from "antd";

import ProTable from "@ant-design/pro-table";
import Page from "components/Page";
import { useAppStore } from "stores";
import { useHistory } from "react-router-dom";
import { _prop } from "_consts";
import {
  EyeOutlined,
  ToTopOutlined,
  DeleteOutlined,
  HistoryOutlined,
  EditOutlined
} from '@ant-design/icons';

export default function () {
  const { api } = useAppStore()
  const history = useHistory()

  const columns = [
    {
      title: "Matricule",
      dataIndex: "matricule",
      copyable: true,
      sorter: true,
    },

    {
      title: "Code GPS",
      dataIndex: "code_gps",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "Genre",
      dataIndex: "genre",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "Groupe",
      dataIndex: "groupName",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "Marque",
      dataIndex: "marque",
      sorter: true,
      hideInSearch: true
    },

    {
      title: "Client",
      dataIndex: "client",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "Conducteur",
      dataIndex: "drivers_fullname",
      sorter: true,
      hideInSearch: true,
      render: (text, row, index, action) => {
        return <span style={{ color: row.date_restitition ? "red" : "green" }}>{row.drivers_fullname}</span>
      }
    },
    {
      title: "Status",
      dataIndex: "state",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "option",
      valueType: "option",
      dataIndex: "id",
      render: (text, row, index, action) => [
        <Tooltip title="Modifier">
          <Button
            onClick={() => {
              history.push("/cars/edit/" + row.id)
            }}
            shape="circle" icon={<EditOutlined />} />
        </Tooltip>,
        <Tooltip title="Historiques">
          <Button
            onClick={() => {
              history.push("/cars/decharges/" + row.id)
            }}
            shape="circle" icon={<HistoryOutlined />} />
        </Tooltip>,
        <Tooltip title="Supprimer">
          <Button
            onClick={() => {
              api.delete("/cars/" + row.id).then(() => { message.info("Bien Supprim"); action.reload(); })
            }}
            shape="circle" icon={<DeleteOutlined />} />
        </Tooltip>
      ]
    }
  ];
  const actionRef = React.useRef()
  window.actionRef = actionRef
  return (
    <Page title={"Vhicules"} selectedSiderKey="list-cars">
      <ProTable
        actionRef={actionRef}
        size="small"
        search={true}
        columns={columns}
        ellipsis={true}
        request={(params, sort, filters) => {
          if (sort) {
            params["sortBy"] = Object.keys(sort)[0]
            params["sort"] = Object.values(sort)[0]
          }
          if (filters) {
            params["filterBy"] = Object.keys(filters)[0]
            params["filter"] = Object.values(filters)
          }
          return api.get("/cars", { params }).then((res) => res.data)
        }}
        pagination={{
          defaultCurrent: 1,
          pageSize: 10
        }}
      />
    </Page>
  );
}

