import React from "react"
import { Form, Input, Button, Checkbox, Select, DatePicker, Rate, InputNumber } from 'antd';
import { values } from "lodash";

import SearchInput from "components/SearchInput"
import CircularProgress from "components/CircularProgress"

const { Option } = Select;
const { TextArea } = Input;

export const FormBuilder = ({ formItems, initialValues = [], onFinish, onFinishFailed }) => {
  const [form] = Form.useForm();
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
  };
  const formTailLayout = {
  };
  return (
    <Form
      {...formItemLayout}
      name="basic"
      form={form}
      layout="vertical"
      initialValues={initialValues}
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
    >
      {
        formItems.map((item, i) => {
          if (item.type === "progress") {
            return <Form.Item
              name={item.name}
              label={item.label}
              hasFeedback
              rules={item.rules}

            >
              <CircularProgress
                defaultValue={initialValues[item.name]}
                onChange={(value) => {
                  form.setFieldsValue(item.name, value)
                }}
              />
            </Form.Item>
          }
          if (item.type === "search") {
            const { renderValue } = item
            return <Form.Item
              name={item.name}
              label={item.label}
              hasFeedback
              rules={item.rules}

            >
              <SearchInput
                url={item.url}
                defaultOption={initialValues[item.defaultOptionName]}
                mapOptionToString={item.mapOptionToString}
                onSelect={(value, option) => {
                  const field = {}
                  field[item.name] = option.id
                  form.setFieldsValue(field)
                }}
                onChange={(value, option) => {
                  if (!option) {
                    const field = {}
                    field[item.name] = null
                    form.setFieldsValue(field)
                  }
                }}
                placeholder={item.label}
              />
            </Form.Item>
          }
          if (item.type === "select") {
            return <Form.Item
              name={item.name}
              label={item.label}
              hasFeedback
              initialValue={item.initialValue}
              rules={item.rules}
            >
              <Select placeholder="Selection">
                {item.selects.map((select, i) => (
                  <Option key={i} value={select.value}>{select.label}</Option>
                ))}
              </Select>
            </Form.Item>
          }
          if (item.type === "text") {
            return <Form.Item
              label={item.label}
              name={item.name}
              rules={item.rules}
            >
              <Input {...item.inputProp} />
            </Form.Item>
          }
          if (item.type === "textarea") {
            return <Form.Item
              label={item.label}
              name={item.name}
              rules={item.rules}
            >
              <TextArea />
            </Form.Item>
          }
          if (item.type === "password") {
            return <Form.Item
              label={item.label}
              name={item.name}
              rules={item.rules}
            >
              <Input.Password />
            </Form.Item>
          }
          if (item.type === "checkbox") {
            return <Form.Item name={item.name} valuePropName="checked">
              <Checkbox>{item.label}</Checkbox>
            </Form.Item>
          }
          if (item.type === "date") {
            return (
              <Form.Item
                label={item.label} name={item.name}>
                <DatePicker format="DD/MM/YYYY" style={{ width: "100%" }} />
              </Form.Item>
            )
          }
          if (item.type === "rate") {
            return <Form.Item label={item.label} name={item.name} >
              <Rate style={{ backgroundColor: "white" }} />
            </Form.Item>
          }
          if (item.type === "integer") {
            return <Form.Item label={item.label} name={item.name} >
              <Input type={"number"} pattern="[0-9]*" {...item.inputProp} />
            </Form.Item>
          }



        })
      }

      <Form.Item {...formTailLayout} >
        <Button type="primary" htmlType="submit">
          Envoyer
          </Button>
      </Form.Item>
    </Form>
  )
}

