import {
  Layout,
  Menu,
  Avatar,
  Popover,
  Button,
  Dropdown,
  message, Tooltip

} from 'antd';

import { DownOutlined, LogoutOutlined, BellOutlined } from '@ant-design/icons';
import { useAppStore } from 'stores';



const NotificatiobContent = () => {
  return (
    <div>
      
    </div>
  )
}

export default () => {
  const { user, setToken, setUser } = useAppStore()

  function handleMenuClick(e) {
    if (e.key === "1") {
      setToken(null)
      setUser(null)
    }
  }
  return (
    <Layout.Header style={{
      height: 48,
      backgroundColor: "white",
      position: 'fixed',
      zIndex: 2,
      width: 'calc(100% - 280px)',
      boxShadow: "0 1px 4px rgba(0,21,41,.08)",
      display: "flex",
      justifyContent: "flex-end",
      alignItems: "center"
    }} >
      <Popover trigger="click" title="Notifications" content={NotificatiobContent}>
        <Button icon={<BellOutlined />} style={{ marginRight: 10 }}>{}</Button>
      </Popover>
      <Dropdown trigger="click" placement="bottomRight" overlay={() => (
        <Menu >
          <Menu.Item onClick={handleMenuClick} key="1" icon={<LogoutOutlined style={{ color: "red" }} />}>
            Se dconnecter
          </Menu.Item>
        </Menu>
      )}>
        <Button>
          {user.username} <DownOutlined />
        </Button>
      </Dropdown>
    </Layout.Header>
  )
}