import * as React from "react";
import moment from "moment";
import { Button, Input, message } from "antd";

import ProTable from "@ant-design/pro-table";
import Page from "components/Page";
import { useAppStore } from "stores";
import { useHistory } from "react-router-dom";


export default function () {
  const { api } = useAppStore()
  const history = useHistory()

  const columns = [
    {
      title: "Code (SAP)",
      dataIndex: "code",
      copyable: true,
      sorter: true
    },
    {
      title: "Designation",
      dataIndex: "designation",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "Localit",
      dataIndex: "localite",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "TEL",
      dataIndex: "tel",
      sorter: true,
      hideInSearch: true,
      copyable: true
    },
    {
      title: "Client Hierachique",
      dataIndex: ["client", "designation"],
      sorter: true,
      hideInSearch: true
    },
    {
      title: "option",
      valueType: "option",
      dataIndex: "id",
      render: (text, row, index, action) => [
        <a
          onClick={() => {
            api.delete("/clients/" + row.id).then(() => { message.info("Bien Supprim"); action.reload(); })
          }}
        >
          Supprimer
      </a>,
        <a
          onClick={() => {
            history.push("/clients/edit/" + row.id)
          }}
        >
          Modifier
      </a>
      ]
    }
  ];

  const actionRef = React.useRef()
  window.actionRef = actionRef
  return (
    <Page title="Clients" selectedSiderKey="list-clients">
      <ProTable
        actionRef={actionRef}
        size="small"
        search={true}
        columns={columns}
        ellipsis={true}
        request={(params, sort, filter) => {
          console.log(sort)
          if (sort) {
            params["sortBy"] = Object.keys(sort)[0]
            params["sort"] = Object.values(sort)[0]
          }
          return api.get("/clients", { params, sort }).then((res) => res.data)
        }}
        pagination={{
          defaultCurrent: 1
        }}
      />
    </Page>
  );
}

