import * as React from "react";
import moment from "moment";
import { Button, Input, message } from "antd";

import ProTable from "@ant-design/pro-table";
import Page from "components/Page";
import { useAppStore } from "stores";
import { useHistory } from "react-router-dom";


export default function () {
  const { api } = useAppStore()
  const history = useHistory()

  const columns = [
    {
      title: "Nom",
      dataIndex: "firstname",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "Prnom",
      dataIndex: "lastname",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "TEL",
      dataIndex: "tel",
      sorter: true,
      hideInSearch: true,
      copyable: true
    },
    {
      title: "Code paie",
      dataIndex: "code_paie",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "Type",
      dataIndex: "type",
      sorter: true,
      hideInSearch: true
    },
    {
      title: "Date Creation",
      dataIndex: "created_at",
      valueType: "dateTime",
      hideInSearch: true
    },
    {
      title: "option",
      valueType: "option",
      dataIndex: "id",
      render: (text, row, index, action) => [
        <a
          onClick={() => {
            api.delete("/drivers/" + row.id).then(() => { message.info("Bien Supprim"); action.reload(); })
          }}
        >
          Supprimer
      </a>,
        <a
          onClick={() => {
            history.push("/drivers/edit/" + row.id)
          }}
        >
          Modifier
      </a>
      ]
    }
  ];

  const actionRef = React.useRef()
  window.actionRef = actionRef
  return (
    <Page title="Utilisateurs"  selectedSiderKey="list-drivers">
      <ProTable
        actionRef={actionRef}
        size="small"
        search={true}
        columns={columns}
        ellipsis={true}
        request={(params, sort, filter) => {
          console.log(sort)
          if (sort) {
            params["sortBy"] = Object.keys(sort)[0]
            params["sort"] = Object.values(sort)[0]
          }
          return api.get("/drivers", { params, sort }).then((res) => res.data)
        }}
        pagination={{
          defaultCurrent: 1
        }}
      />
    </Page>
  );
}

